:root {
  --primary-color: #8B5E3C;
  --background-color: #f4e7e1;
  --text-color: #8B5E3C;
}

body {
  margin: 0;
  font-family: Arial, sans-serif;
  display: flex;
  justify-content: center;
  align-items: flex-start; /* Cambiado de center a flex-start */
  height: 100vh;
  background-color: var(--background-color);
  overflow: auto; /* A単adido para permitir el desplazamiento si es necesario */
}

.container {
  max-width: 1200px;
  margin: 20px; /* A単adido margen superior para espacio */
  padding: 20px;
  display: flex;
  flex-direction: column;
  background-color: var(--background-color);
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  overflow: hidden; /* A単adido para evitar el desbordamiento */
}

.table-container {
  max-height: 400px;
  overflow-y: auto;
}

.content {
  padding: 20px;
  text-align: center;
}

.content h1 {
  color: #FF4500;
  font-size: 24px;
}

.content h2 {
  color: var(--primary-color);
  font-size: 36px;
}

.content p {
  font-size: 18px;
  margin-bottom: 20px;
  color: var(--primary-color);
}

.botonera {
  display: flex;
  flex-direction: column;
}

.btn, .new-client-button {
  border: 2px solid var(--primary-color);
  margin-left: 20px;
  margin-top: 10px;
  padding: 10px 20px;
  border-radius: 8px;
  background-color: var(--background-color);
  font-size: 16px;
  color: var(--primary-color);
  text-transform: capitalize;
  cursor: pointer;
  text-decoration: none;
}

.btn:hover, .new-client-button:hover {
  background-color: var(--primary-color);
  color: #ddd;
}

.image {
  width: 100%;
  height: 400px;
  background-image: url('imagen/agustina-pesta単as.jpg');
  background-size: cover;
  background-position: center;
  border-radius: 8px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.select-month {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.select-month label {
  font-size: 18px;
  color: var(--primary-color);
  margin-right: 10px;
}

.select-month select {
  padding: 5px;
  font-size: 16px;
}

.title {
  text-align: center;
  padding-left: 100px;
  margin-bottom: 20px;
}

.title h1 {
  color: #FF4500;
  font-size: 24px;
  margin: 0;
}

.title h2 {
  color: var(--primary-color);
  font-size: 36px;
  margin: 5px 0;
}

.title p {
  font-size: 18px;
  color: var(--primary-color);
}

.stats {
  padding-bottom: 30px;
}

.stats p {
  font-size: 18px;
  color: var(--primary-color);
}

.client-list label {
  font-size: 18px;
  color: var(--primary-color);
  margin-right: 10px;
}

.client-list select {
  padding: 5px;
  font-size: 16px;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
  text-align: left;
}

th, td {
  padding: 10px;
  border: 1px solid var(--primary-color);
}

th {
  background-color: var(--primary-color);
  color: #f4e7e1;
}

td {
  color: var(--primary-color);
}


/* New cliente */
.new-client {
  margin-top: 20px;
}

.new-client h3 {
  font-size: 24px;
  color: #8B5E3C;
}

.new-client table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
}

.new-client td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: center;
  color: #8B5E3C;
  font-size: 18px;
}

/* Botones */
.new-client td button {
  padding: 5px 7px;
  background-color: #007BFF;
  color: #fff;
  border: none;
  cursor: pointer;
  margin-bottom: 5px;
  width: 100%;
  display: block;  
}

.new-client td button:hover {
  background-color: #0056b3;
}

.btn {
  padding: 5px 25px;
  background-color: #6775f8;
  color: #ffffff;
  border-radius: 10px;
  display: inline-block;
  font-size: 16px;
  text-transform: capitalize;
  box-shadow: 0 0 20px rgb(0, 0, 0);
  margin: 10px;
  cursor: pointer;
}

.btn:hover {
  background-color: #202447;
}

/* Input$ */
.inputpeso {  
  border: none;
  background-color: transparent;
  text-align: center;
  color: #8B5E3C;
  font-size: 20px;
}

.inputText {  
  border: none;
  text-align: center;
  color: #8B5E3C;
  font-size: 20px;
}

/* Media Queries */
@media (max-width: 768px) {
  .container {
    
    align-items: center;
    padding: 10px;
  }

  .header {
    flex-direction: column;
    align-items: flex-start;
  }

  .botonera {
    flex-direction: column;
    margin-left: 0;
  }

  .btn, .new-client-button {
    margin-left: 0;
    margin-top: 10px;
    width: 100%;
  }

  .title {
    padding-left: 0;
  }

  .title h1, .title h2 {
    font-size: 28px;
  }

  .title p {
    font-size: 16px;
  }

  .content h1 {
    font-size: 20px;
  }

  .content h2 {
    font-size: 28px;
  }

  .content p {
    font-size: 16px;
  }

  .select-month {
    flex-direction: column;
    align-items: flex-start;
  }

  .select-month label, .select-month select {
    font-size: 16px;
  }
}

@media (max-width: 480px) {
  .container {
    padding: 5px;
    align-items: center;
  }

  .title h1, .title h2 {
    font-size: 24px;
  }

  .title p {
    font-size: 14px;
  }

  .content h1 {
    font-size: 18px;
  }

  .content h2 {
    font-size: 24px;
  }

  .content p {
    font-size: 14px;
  }

  .btn, .new-client-button {
    font-size: 14px;
  }

  .inputpeso, .inputText {
    font-size: 18px;
  }
}
